import React, { useState, useEffect } from "react";
import { Tabs } from "antd";
import axios from "axios";
import Success from "../components/Success";
const { TabPane } = Tabs;

function AdminScreen() {
  return (
    <div>
      <div className="container bs">
        <h3>Admin Panel</h3>
        <Tabs defaultActiveKey="1">
          <TabPane tab="Bookings" key="1">
            <Bookings />
          </TabPane>
          <TabPane tab="Rooms" key="2">
            <Rooms />
          </TabPane>
          <TabPane tab="Add Rooms" key="3">
            <NewRoom />
          </TabPane>
          <TabPane tab="Users" key="4">
            <Users />
          </TabPane>
        </Tabs>
      </div>
    </div>
  );
}

function Bookings() {
  const [bookings, setBookings] = useState([]);
  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const { data } = await axios.get("/api/bookings/getallbookings");
        setBookings(data);
      } catch (error) {
        console.error("Error fetching bookings:", error);
      }
    };

    fetchBookings();
  }, []);

  const deleteAllBookings = async () => {
    try {
      await axios.delete("/api/bookings/deleteallbookings");
      setBookings([]); // Clear the bookings from the state after deletion
      console.log("All bookings deleted");
    } catch (error) {
      console.error("Error deleting bookings:", error);
    }
  };
  

  

  return (
    <div>
      <div className="row">
        <h3>Bookings</h3>
        <p>There are total {bookings.length} bookings</p>
        <table className="table">
          <thead>
            <tr>
              <th scope="col">No</th>
              <th scope="col">Booking Id</th>
              <th scope="col">User Id</th>
              <th scope="col">User </th>
              <th scope="col">Room</th>
              <th scope="col">From</th>
              <th scope="col">To</th>
              <th scope="col">Status</th>
              <th scope="col">Total Amount</th>
            </tr>
          </thead>
          <tbody>
            {bookings.map((booking, index) => (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{booking._id}</td>
                <td>{booking.userid}</td>
                <td>{booking.username}</td>
                <td>{booking.roomname}</td>
                <td>{booking.fromdate}</td>
                <td>{booking.todate}</td>
                <td>{booking.status}</td>
                <td>Rs {booking.totalrent}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <button className="btn btn-sm btn-primary" onClick={deleteAllBookings}>Delete</button>
    </div>
  );
}

function Rooms() {
  const [rooms, setRooms] = useState([]);

  useEffect(() => {
    const fetchrooms = async () => {
      try {
        const { data } = await axios.get("/api/rooms/getallrooms");
        setRooms(data);
      } catch (error) {
        console.error("Error fetching rooms:", error);
      }
    };

    fetchrooms();
  }, []);

  return (
    <div>
      <div className="row">
        <h3>Bookings</h3>
        <p>There are total {rooms.length} rooms</p>
        <table className="table">
          <thead>
            <tr>
              <th scope="col">No</th>
              <th scope="col">Room Id</th>
              <th scope="col">Room name</th>
              <th scope="col">Max count</th>
              <th scope="col">Phone number</th>
              <th scope="col">Rent per day</th>
              <th scope="col">Type</th>
            </tr>
          </thead>
          <tbody>
            {rooms.map((rooms, index) => (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{rooms._id}</td>
                <td>{rooms.name}</td>
                <td>{rooms.maxcount}</td>
                <td>{rooms.phonenumber}</td>
                <td>{rooms.rentperday}</td>
                <td>{rooms.type}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

function Users() {
  const [users, setUsers] = useState([]);

  useEffect(() => {
    const fetchusers = async () => {
      try {
        const { data } = await axios.get("/api/users/getallusers");
        setUsers(data);
      } catch (error) {
        console.error("Error fetching users:", error);
      }
    };

    fetchusers();
  }, []);

  return (
    <div>
      <div className="row">
        <h3>Users</h3>
        <p>There are total {users.length} rooms</p>
        <table className="table">
          <thead>
            <tr>
              <th scope="col">No</th>
              <th scope="col">User Id</th>
              <th scope="col">User name</th>
              <th scope="col">Email</th>
              <th scope="col">Password</th>
            </tr>
          </thead>
          <tbody>
            {users.map((users, index) => (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{users._id}</td>
                <td>{users.name}</td>
                <td>{users.email}</td>
                <td>{users.password}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

function NewRoom() {
  const [name, setname] = useState("");
  const [maxcount, setmaxcount] = useState("");
  const [phonenumber, setphonenumber] = useState("");
  const [rentperday, setrentperday] = useState("");
  const [type, settype] = useState();
  const [description, setdescription] = useState();
  const [imgurls1, setimgurls1] = useState();
  const [imgurls2, setimgurls2] = useState();
  const [imgurls3, setimgurls3] = useState();
  const [success, setSuccess] = useState();

  async function AddRoom() {
    const room = {
      name,
      maxcount,
      phonenumber,
      rentperday,
      type,
      description,
      imageurls: [imgurls1, imgurls2, imgurls3],
    };
    try {
      const { data } = await axios.post("/api/rooms/postnewroom", room);
      setSuccess(true);
      console.log("New Rooms Details:", data);
    } catch (error) {
      console.log(error);
    }
  }
  return (
    <div>
      <div className="row justify-content-around d-flex">
        <h1 className="text-center">Add new room </h1>
        {success && <Success message={"Registration succesful!"} />}
        <div className="col-md-5  mt-5 bs">
          <div>
            <hr />
            <input
              type="text"
              className="form-control"
              placeholder="name"
              value={name}
              onChange={(e) => {
                setname(e.target.value);
              }}
            ></input>
            <br></br>
            <input
              type="text"
              className="form-control"
              placeholder="maxcount"
              value={maxcount}
              onChange={(e) => {
                setmaxcount(e.target.value);
              }}
            ></input>
            <br></br>
            <input
              type="text"
              className="form-control"
              placeholder="phonenumber"
              value={phonenumber}
              onChange={(e) => {
                setphonenumber(e.target.value);
              }}
            ></input>
            <br></br>
            <input
              type="text"
              className="form-control"
              placeholder="rentperday"
              value={rentperday}
              onChange={(e) => {
                setrentperday(e.target.value);
              }}
            ></input>
            <br></br>
            <input
              type="text"
              className="form-control"
              placeholder="type"
              value={type}
              onChange={(e) => {
                settype(e.target.value);
              }}
            ></input>
            <br></br>
          </div>
        </div>
        <div className="col-md-5  mt-5 bs">
          <div>
            <hr />
            <input
              type="text"
              className="form-control"
              placeholder="description"
              value={description}
              onChange={(e) => {
                setdescription(e.target.value);
              }}
            ></input>
            <br></br>
            <input
              type="text"
              className="form-control"
              placeholder="image url 1"
              value={imgurls1}
              onChange={(e) => {
                setimgurls1(e.target.value);
              }}
            ></input>
            <br></br>
            <input
              type="text"
              className="form-control"
              placeholder="img urls 2"
              value={imgurls2}
              onChange={(e) => {
                setimgurls2(e.target.value);
              }}
            ></input>
            <br></br>
            <input
              type="text"
              className="form-control"
              placeholder="img urls 3"
              value={imgurls3}
              onChange={(e) => {
                setimgurls3(e.target.value);
              }}
            ></input>
            <br></br>
          </div>
          <button className="btn btn-primary" onClick={AddRoom}>
            <>Add</>
          </button>
        </div>
      </div>
    </div>
  );
}

export default AdminScreen;
