import React from "react";

function Navbar() {
  // Fetch the user from localStorage
  const user = JSON.parse(localStorage.getItem("currentUser"));
  const handleLogout = () => {
    localStorage.removeItem("currentUser"); // Clear the user data from localStorage
    window.location.href = "/login"; // Redirect to the login page
  };

  return (
    <nav className="navbar navbar-expand-lg sticky-top" style={{backgroundColor:"black"}}>
      <div className="container-fluid">
        <a className="navbar-brand" href="/">
          <b>Yoyo Rooms</b>
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav ms-auto">
            {user ? (
              <>
                <div class="btn-group dropstart">
                  <button
                    type="button"
                    class="btn btn-secondary dropdown-toggle"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    {user.name}
                  </button>
                  <ul class="dropdown-menu">
                    <li>
                      <a className="dropdown-item" href="/profile">
                        Profile
                      </a>
                    </li>
                    <li>
                      <a className="dropdown-item" href="/profile">
                        Bookings
                      </a>
                    </li>
                    <li>
                      <a className="dropdown-item" href="/">
                        Home
                      </a>
                    </li>
                    <li>
                      <hr className="dropdown-divider" />
                    </li>
                    <li>
                      <a
                        className="dropdown-item"
                        onClick={handleLogout}
                        href="#"
                      >
                        Logout
                      </a>
                    </li>
                  </ul>
                </div>
              </>
            ) : (
              <>
                <li className="nav-item">
                  <a
                    className="nav-link navbar-names active"
                    aria-current="page"
                    href="/register"
                 >
                    Register
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link navbar-names" href="/login">
                    Login
                  </a>
                </li>
                
              </>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
