import React, { useState, useEffect } from "react";
import axios from "axios";
import { useParams } from "react-router-dom";
import Button from "react-bootstrap/Button";
import moment from "moment";
import { useNavigate } from "react-router-dom";

function Bookingscreen() {
  const [room, setRoom] = useState(null);
  const { roomid, fromdate, todate } = useParams();
  const fromDateMoment = moment(fromdate, "DD-MM-YYYY");
  const toDateMoment = moment(todate, "DD-MM-YYYY");
  const totalDays = toDateMoment.diff(fromDateMoment, "days") + 1;
  const totalAmount = room ? room.rentperday * totalDays : 0;
  const currentUser = JSON.parse(localStorage.getItem("currentUser"));
  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.post("/api/rooms/getroombyid", { roomid });
        setRoom(data);
      } catch (error) {
        console.error("Error fetching room data:", error);
      }
    };

    fetchData();
  }, [roomid]);

  async function bookRoom() {
    try {
      // Step 1: Create a Razorpay order
      const { data } = await axios.post("/api/razorpay/order", {
        amount: totalAmount, // Amount in rupees
      });

      // Step 2: Initialize Razorpay Checkout
      const options = {
        key: "rzp_test_NVSX8r60FD8cbc", // Replace with your Razorpay key
        amount: data.amount, // Amount in the smallest currency unit (e.g., paise)
        currency: data.currency,
        name: "Room Booking",
        description: "Booking Room",
        order_id: data.id,
        handler: async function (response) {
          try {
            // Verify the payment
            await axios.post("/api/razorpay/verify", {
              razorpay_payment_id: response.razorpay_payment_id,
              razorpay_order_id: response.razorpay_order_id,
              razorpay_signature: response.razorpay_signature,
            });

            // Proceed with booking the room
            const bookingDetails = {
              room,
              roomid,
              user: currentUser ? currentUser._id : null,
              username: currentUser ? currentUser.name : null,
              fromdate,
              todate,
              totalAmount,
              totalDays,
            };

            await axios.post("/api/bookings/bookroom", bookingDetails);
            navigate(`/book/${roomid}/${fromdate}/${todate}/booking-success`);
          } catch (error) {
            console.error("Error verifying payment:", error);
          }
        },
        prefill: {
          name: currentUser ? currentUser.name : "Guest",
          email: currentUser ? currentUser.email : "",
        },
      };

      const paymentObject = new window.Razorpay(options);
      paymentObject.open();
    } catch (error) {
      console.error("Error creating order:", error);
    }
  }

  if (!room) {
    return <p>Loading...</p>;
  }

  return (
    <div className="container mt-5">
      <div className="row d-flex justify-content-between bs">
        <div className="col-md-5">
          <h2>{room.name}</h2>
          <img
            className="bs"
            src={room.imageurls[0]}
            alt={room.name}
            style={{ width: "100%", height: "50vh" }}
          />
        </div>
        <div className="col-md-5">
          <b>
            <h4>Booking Details</h4>
          </b>
          <hr />
          <p>Name: {currentUser ? currentUser.name : "Guest"}</p>
          <p>From Date: {fromdate}</p>
          <p>To Date: {todate}</p>
          <p>Max Count: {room.maxcount}</p>
          <br />
          <br />
          <b>
            <h4>Amount</h4>
          </b>
          <hr />
          <p>Rent Per Day: {room.rentperday} Rs</p>
          <p>Total Days: {totalDays}</p>
          <h5>Total Amount: {totalAmount} Rs</h5>
          <div className="mt-3">
            <Button  className="btn btn-success" variant="secondary" onClick={bookRoom}>
              Pay Now
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Bookingscreen;
