import React, { useEffect, useState } from "react";
import axios from "axios";

const currentUser = JSON.parse(localStorage.getItem("currentUser"));

function ProfileScreen() {
  const [bookings, setBookings] = useState([]); // State to hold the bookings data
  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const response = await axios.get("/api/bookings/getbookingbyuserid", {
          params: { userid: currentUser._id }, // Pass userid as query parameter
        });
        setBookings(response.data); // Update the state with the bookings data
      } catch (error) {
        console.log(error);
      }
    };

    fetchBookings();
  }, []);

  const deleteOneBookings = async () => {
    try {
      await axios.delete("/api/bookings/deleteonebookings");
      setBookings([]); // Clear the bookings from the state after deletion
      console.log(" bookings deleted");
    } catch (error) {
      console.error("Error deleting bookings:", error);
    }
  };
  return (
    <div>
      <div className="container bs profile">
        <div className="row">
          <div className="col">
            <b>Profile</b>
            <hr />
            <div className="col mt-4">
              Name: {currentUser.name}
              <br />
              <br />
              Email: {currentUser.email}
              <br />
              <br />
              Password:{currentUser.password}
              <br />
              <br />
              ID: {currentUser._id}
            </div>
          </div>
        </div>
      </div>
      <div className="container bs booking">
        <div className="row">
          <div className="col">
            <b>My Bookings</b>
            <hr />
            <div className="col mt-4">
              {bookings.length > 0 ? (
                bookings.map((bookings) => (
                  <div key={bookings._id}>
                    <p>Booking ID: {bookings._id}</p>
                    <p>Room Name: {bookings.roomname}</p>
                    <p>Check-In Date:{bookings.fromdate}</p>
                    <p>Check-Out Date:{bookings.todate}</p>
                    <p>Total Amount Paid: {bookings.totalrent}</p>
                    <p>Status: {bookings.status}</p>
                    <button
                      className="btn btn-danger float-end"
                      onClick={deleteOneBookings}
                    >
                      Cancel
                    </button>
                    <br />
                    <br />
                    <hr />
                  </div>
                ))
              ) : (
                <p>No bookings found.</p>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProfileScreen;
