import React, { useState } from "react";
import axios from "axios";
import Success from "../components/Success";
import Error from "../components/Error";

function Loginscreen() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState();
  const [success, setSuccess] = useState();

  // hide password funcction
  function hidePassword() {
    const pass = document.getElementById("Password");
    if (pass.type === "password") {
      pass.type = "text";
    } else {
      pass.type = "password";
    }
  }

  // Login Function Starts Here
  async function Login() {
    const user = {
      email,
      password,
    };

    try {
      setSuccess(false); // Reset success state
      const { data } = await axios.post("/api/users/login", user);
      setSuccess(true);
      setError(false);
      console.log("User details:", data);
      localStorage.setItem("currentUser", JSON.stringify(data));

      // Check if the logged-in user is the admin
      if (email === "admin@gmail.com") {
        localStorage.setItem("isAdmin", true);
        // Redirect to admin screen
        window.location.href = "/admin";
      } else {
        localStorage.setItem("isAdmin", false);
        // Redirect to home screen
        window.location.href = "/";
      }
    } catch (error) {
      setSuccess(false);
      setError(true);
      console.log(error);
    }
  }

  return (
    <div>
      <div className="row justify-content-center">
        <div className="col-md-5 mt-5 bs">
          <div className="text-center">
            <h1>Login</h1>
            {success && <Success message={"Login successful!"} />}
            {error && <Error message={"Incorrect credentials"} />}
            <hr />
            <input
              type="email"
              className="form-control"
              placeholder="Email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <br />
            <input
              id="Password"
              type="password"
              className="form-control"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <br />
          </div>
          <div>
            <label>
              <input type="checkbox" onClick={hidePassword} />
              Show Password
            </label>
          </div>
          <button className="btn btn-primary" onClick={Login}>
            Login
          </button>
          <br />
          <br />
          <span className="fs-25">
            <p>
              <a
                className="link-offset-2 link-underline link-underline-opacity-0"
                href="/register"
              >
                Don't have an account yet? Sign Up
              </a>
            </p>
          </span>
        </div>
      </div>
    </div>
  );
}

export default Loginscreen;
