import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Carousel from "react-bootstrap/Carousel";
import {useNavigate } from "react-router-dom";

function Room({ room, fromdate, todate, showAlert }) {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const history = useNavigate();
  const handleBookNow = () => {
    if (!fromdate || !todate) {
      showAlert(  );
    } else {
      history(`/book/${room._id}/${fromdate}/${todate}`);
    }
  };

  return (
    <div className="row bs">
      <div className="col md-4">
        <img src={room.imageurls[0]} className="smallimg" alt="Room" />
      </div>
      <div className="col md-7">
        <b>
          <h3>{room.name}</h3>
          <p>Max Count: {room.maxcount}</p>
          <p>Rent Per Day: {room.rentperday} Rs</p>
          <p>Room Type: {room.type}</p>
          <p>Phone-Number: {room.phonenumber}</p>
        </b>

        <div className="d-flex justify-content-end">
          <button className="btn btn-primary mx-2" onClick={handleBookNow}>
            Book Now
          </button>
          <button className="btn btn-primary mx-2" onClick={handleShow}>
            View Details
          </button>
        </div>
      </div>

      {/* View Deteials modal box  */}
      <Modal show={show} onHide={handleClose} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>{room.name}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div style={{ display: "block", padding: 10 }}>
            <Carousel>
              {room.imageurls && room.imageurls.length > 0 ? (
                room.imageurls.map((url, index) => (
                  <Carousel.Item key={index} interval={500}>
                    <img
                      className="d-block w-100 smallimg"
                      src={url}
                      alt={`Image ${index + 1}`}
                    />
                  </Carousel.Item>
                ))
              ) : (
                <p>No images available</p>
              )}
            </Carousel>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Modal.Body>{room.description}</Modal.Body>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <button className="btn btn-primary mx-2" onClick={handleBookNow}>
            Book Now
          </button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default Room;
