import React from "react";
import BarLoader from "react-spinners/BarLoader";

function Loader({ loading, color = "#000000", size = 80 }) {
  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        height: "100vh",
      }}
    >
      <BarLoader
        color={color}
        loading={loading}
        width={size}
        height={5} // Standard height for BarLoader
        aria-label="Loading Spinner"
        data-testid="loader"
      />
    </div>
  );
}

export default Loader;
