import React, { useState, useEffect } from "react";
import axios from "axios";
import Room from "../components/Room";
import Loader from "../components/Loader";
import Error from "../components/Error";
import { DatePicker, Input } from "antd";

function Homescreen() {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const { RangePicker } = DatePicker;
  const [rooms, setRooms] = useState([]);
  const [fromdate, setFromdate] = useState("");
  const [todate, setTodate] = useState("");

  useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get("/api/rooms/getallrooms");
        setRooms(data);
      } catch (error) {
        setError(true);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const filterByDate = (dates) => {
    if (dates && dates.length === 2) {
      const [start, end] = dates;
      setFromdate(start.format("DD-MM-YYYY"));
      setTodate(end.format("DD-MM-YYYY"));
      console.log(`Selected From Date: ${start.format("DD-MM-YYYY")}`);
      console.log(`Selected To Date: ${end.format("DD-MM-YYYY")}`);
    } else {
      setFromdate("");
      setTodate("");
      console.log("Date range not selected");
    }
  };

  const isLoadingOrError = loading || error;
  const hasRooms = !isLoadingOrError && rooms.length > 0;
  const showAlert = () => {
    alert("Please select a date range first.");
  };

  return (
    <div className="container">
      <div
        className="row justify-content-center align-items-center bs"
        style={{ marginTop: 50 }}
      >
        <div className="col-md-3">
          <RangePicker
            format="DD-MM-YYYY"
            onChange={filterByDate}
            placeholder={["Select Start Date", "Select End Date"]}
          />
        </div>
        <div className="col-md-3">
          <Input placeholder="Search rooms" />
        </div>
        <div className="col-md-3">
          <Input placeholder="Search All    " />
        </div>
      </div>

      <div className="row justify-content-center mt-5">
        {isLoadingOrError ? (
          loading ? (
            <Loader />
          ) : (
            <Error />
          )
        ) : !hasRooms ? (
          <h1>No rooms available</h1>
        ) : (
          rooms.map((room) => (
            <div className="row md-12" key={room._id}>
              <Room
                room={room}
                fromdate={fromdate}
                todate={todate}
                showAlert={showAlert}
              />
            </div>
          ))
        )}
      </div>
    </div>
  );
}

export default Homescreen;
